import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import util from 'preact-util';

import DragNDropFileupload from './dragNdropFileupload';

@observer
class Images extends Component {
    constructor(props) {
        super(props);
        this.state = {
            files: [],
        };
    }
    
    handleAddImage = (file) => {
        // <Images saved={saved} object={dog} field={field} updateField={updateField} />
        // updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const {
            updateField,
            object,
            field,
        } = this.props;
        updateField({ id: object.id, field, value: file });

        let images = toJS(object.images);
        if (!util.isArray(images)) {
            images = [];
        }
        images.push(file);
        updateField({ id: object.id, field: 'images', value: images });
    };

    render() {
        return (
            <div class='row'>
                <div class='col-12'>
                    Last opp fil:<br />
                    <DragNDropFileupload
                        handleAddImage={this.handleAddImage}
                        apiUrl={'/api/fileupload'}
                    >
                        <div style={{ height: 30, width: '100%' }}>
                            {/* {files.map((file, idx) => (
                                <div key={idx}>
                                    {file.name}
                                    Total: {util.asString(file, 'load', 'total')}
                                    <img style='width: 48px;'
                                        alt='Preview'
                                        key={file.preview}
                                        src={file.src}
                                    />
                                </div>
                            ))} */}
                        </div>
                    </DragNDropFileupload>
                </div>
            </div>
        );
    }
}

export default Images;
