import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';

configure({ enforceActions: 'always' });

class TrackStore extends StoreModel {
    constructor() {
        super('track', {
            namePlural: 'tracks',
            sort: 'title',
            limit: 100,
            api: {
                search: {
                    url: '/api/tracks/',
                    params: {
                        extendedView: 1,
                        limit: 15,
                        sort: 'id',
                    },
                },
                load: {
                    url: '/api/tracks/',
                    params: {},
                },
                save: {
                    url: '/api/tracks/',
                    params: {},
                },
            },
        });
    }

    @observable track = {};

    @observable newTrack = {};

    @observable tracks = [];

    // @action
    // afterLoad(response) {
    //     console.log(response);
    // }
}

const store = new TrackStore();
export default store;
