import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';

import Error from '../error';
import Message from '../message';

@observer
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showToken: false,
            isCapsLock: false,
            email: this.props.email,
            code: '',
        };
    }

    handleEmailInput = async (e) => {
        const { userStore } = this.props.stores;
        const email = e.target.value;
        this.setState({ email });
        clearTimeout(this.checkEmailTimer);
        this.checkEmailTimer = setTimeout(() => userStore.checkEmail(email), 1000);
    }

    updateCapsLockStatus = (isCapsLock) => {
        this.setState({
            isCapsLock,
        });
    }

    sendLoginLink = () => {
        const { email = '' } = this.state;
        const { userStore } = this.props.stores;
        userStore.sendLoginLink(email);
    }

    validateCode = () => {
        const { email, code } = this.state;
        const { userStore } = this.props.stores;
        userStore.validateLoginCode(email, code);
    }

    resetLoginLinkMessages = () => {
        const { userStore } = this.props.stores;
        userStore.resetLoginLinkMessages();
    }

    resetLoginMessages = () => {
        const { userStore } = this.props.stores;
        userStore.resetLoginMessages();
    }

    resetEmailSent = () => {
        const { userStore } = this.props.stores;
        userStore.updateObjectField('loginLink', 'emailSent', false);
    }

    newUser = () => {
        const { email = '' } = this.state;
        route(`/register/${email}`, true);
    }

    login = () => {
        const { email = '' } = this.state;
        route(`/${email}`, true);
    }

    componentDidMount() {
        const { userStore } = this.props.stores;
        if (this.props.email) {
            clearTimeout(this.checkEmailTimer);
            this.checkEmailTimer = setTimeout(() => userStore.checkEmail(this.props.email), 1000);
        }
    }

    render() {
        const { isCapsLock, email, code } = this.state;
        const { userStore } = this.props.stores;
        const { emailIsAvailable, loginLink, login } = userStore;
        const { emailSent, message, messageIcon, error, errorIcon } = loginLink;
        const {
            message: loginMessage, messageIcon: loginMessageIcon,
            error: loginError, errorIcon: loginErrorIcon
        } = login;

        const isValidEmail = util.validateEmail(email);
        const isOkToSendLink = isValidEmail && !emailIsAvailable;
        const isValidCode = util.validateCode(code, 100000, 999999);

        if (emailSent) {
            return (
                <div class='container-fluid'>
                    <div class='row'>
                        <div class='col-4 offset-4'>
                            <h1 class='mt-5'><i class='fas fa-paw' /> The Musher</h1>
                            <h5 class='mt-1 mb-5'>Glemt passord</h5>

                            <Error message={error} icon={errorIcon} remove={this.resetLoginLinkMessages} />
                            <Error message={loginError} icon={loginErrorIcon} remove={this.resetLoginMessages} />
                            <Message message={message} icon={messageIcon} remove={this.resetLoginLinkMessages}  />
                            <Message message={loginMessage} icon={loginMessageIcon} remove={this.resetLoginMessages}  />

                            <div class='form-group'>
                                <label for='codeInput'><i class='fas fa-unlock-alt' /> Engangskode for {email}</label>
                                <input type='text'
                                    class={`form-control ${code ? (isValidCode ? 'is-valid' : 'is-invalid') : ''}`}
                                    style='font-size: 40px; letter-spacing: 5px'
                                    id='codeInput'
                                    onInput={linkState(this, 'code')}
                                    value={code}
                                />
                                {code && !isValidCode && <div class="invalid-feedback text-white" style={'display: block;'}>
                                    Hmm, koden ser ikke helt riktig ut. Kan det være en skrivefeil?
                                </div>}
                                {code && isValidCode && !emailIsAvailable && <div class="invalid-feedback text-white" style={'display: block;'}>
                                    Koden ser bra ut.
                                </div>}
                            </div>

                            <button class={`btn ${isValidCode ? 'btn-success' : 'btn-outline-success'}`} type='button'
                                onClick={this.validateCode}
                                disabled={!isValidCode}
                            >
                                <i class='far fa-paper-plane mr-2' />
                                Logg meg inn med koden
                            </button>


                            <small class='form-text text-muted mt-5 text-center'>
                                <button class='btn btn-small btn-link'
                                    onClick={this.login}
                                >
                                    Jeg husker passordet. Gå til logg inn.
                                </button>
                            </small>

                            <small class='form-text text-muted mt-2 text-center'>
                                <button class='btn btn-small btn-link'
                                    onClick={this.resetEmailSent}
                                >
                                    Jeg trenger en ny kode.
                                </button>
                            </small>

                        </div>
                    </div>
                </div>
            );
        }

        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='col-4 offset-4'>
                        <h1 class='mt-5'><i class='fas fa-paw' /> The Musher</h1>
                        <h5 class='mt-1 mb-5'>Glemt passord</h5>

                        <Error message={error} icon={errorIcon} remove={this.resetLoginLinkMessages} />
                        <Message message={message} icon={messageIcon} remove={this.resetLoginLinkMessages}  />

                        <div class='form-group'>
                            <label for='emailInput'><i class='far fa-envelope' /> E-post</label>
                            <input type='email'
                                class={`form-control ${email ? (isValidEmail && !emailIsAvailable ? 'is-valid' : 'is-invalid') : ''}`}
                                id='emailInput'
                                onInput={this.handleEmailInput}
                                value={email}
                                onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                            />
                            {email && isValidEmail && emailIsAvailable && <div class='invalid-feedback text-white' style={`display: block;`}>
                                E-posten finnes ikke, men du kan <a href={`/register/${email}`}>registrere deg</a> 😃
                            </div>}
                            {email && !isValidEmail && <div class="invalid-feedback text-white" style={'display: block;'}>
                                Hmm, e-posten ser ikke helt riktig ut. Kan det være en skrivefeil?
                            </div>}
                            {email && isValidEmail && !emailIsAvailable && <div class="invalid-feedback text-white" style={'display: block;'}>
                                E-post ser bra ut.
                            </div>}
                        </div>


                        {isCapsLock && <div class='alert alert-danger' role='alert'>
                            CAPS LOCK er på!
                        </div>}

                        <button class={`btn ${isOkToSendLink ? 'btn-success' : 'btn-outline-success'}`} type='button'
                            onClick={this.sendLoginLink}
                            disabled={!isOkToSendLink}
                        >
                            <i class='far fa-paper-plane mr-2' />
                            Send meg en reset link
                        </button>

                        <small class='form-text text-muted mt-5 text-center'>
                            <button class='btn btn-small btn-link'
                                onClick={this.login}
                            >
                                Jeg husker passordet. Gå til logg inn.
                            </button>
                        </small>

                    </div>
                </div>
            </div>
        );
    }
}

export default Login;
