import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';

import Images from '../../components/form/images';

const initialState = {
    newDog: {},
};

@observer
class Input extends Component {
    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        const { saved, dog, type = 'text', datalist = [], min, max, step, field, title, icon, help, onInput, updateField } = this.props;
        let value = dog[field];
        let maxValue = max;
        if (type === 'date') {
            if (value) {
                value = util.isoDate(dog[field], false, false, true);
            }
            if (max === 'today') {
                maxValue = util.isoDate(undefined, false, false, true);
            }
        }
        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    {dog.image && <div><img src={dog.image.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} /></div>}
                    <Images saved={saved} object={dog} field={field} updateField={updateField} />
                    {saved[`${field}.${dog.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'datalist') {
            return (
                <div class={`form-group ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    <input list={`list-${field}`} class={`form-control ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        data-id={dog.id}
                        data-field={field}
                        onInput={onInput}
                    />
                    <datalist id={`list-${field}`} >
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </datalist>
                    {saved[`${field}.${dog.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'select') {
            return (
                <div class={`form-group ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>

                    <select type={type} class={`form-control ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        data-id={dog.id}
                        data-field={field}
                        onInput={onInput}
                    >
                        <option value=''>-- Velg kjønn --</option>
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </select>
                    {saved[`${field}.${dog.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        return (
            <div class={`form-group ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                <input type={type} class={`form-control ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    min={min}
                    max={maxValue}
                    step={step}
                    value={value}
                    data-id={dog.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${dog.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class DogsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore } = this.props.stores;
        if (this.props.dog === 'new') {
            this.setState({ createNew: true });
        } else {
            dogStore.load(this.props.dog);
        }
    }

	resetDog() {
		this.setState({
            newDog: {},
            createNew: false,
        });
	}

	insertDog = async () => {
		const { newDog } = this.state;
		const { dogStore } = this.props.stores;
		await dogStore.insert({ ...newDog });
        this.resetDog();
        route(`/dogs/`, true);
	}

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { dogStore } = this.props.stores;
        let newValue = value;
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            dogStore.updateObjectKeyValue('newDog', field, value);
        } else {
            dogStore.updateField(id, field, newValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { dogStore } = this.props.stores;
        await dogStore.saveField(id, field, value);
    }

    back() {
        route('/dogs/', true);
    }

    componentWillMount() {
        this.loadDog();
    }

	render() {
        const { createNew, newDog } = this.state;
		const { dogStore } = this.props.stores;
		const { insertStatus, saved, dog } = dogStore;
        const { name } = dog;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
                        {createNew ? (
                            <div> 
                                <h5>Legg til ny hund</h5>
                                <div class='form-group'>
                                    <label for='nameInput'>Navn</label>
                                    <input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
                                        onInput={linkState(this, 'newDog.name')}
                                        value={newDog.name}
                                    />
                                    <small id='nameHelp' class='form-text text-muted'>Fullt navn på hunden.</small>
                                </div>
                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertDog}
                                >
                                    Registrer ny hund
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h5>Endre {name}</h5>
                                <Input saved={saved} dog={dog} field='image' type='image' title='Bilde' help='Link til bilde av hunden.' onInput={this.updateField} updateField={this.updateFieldValue} />
                                <Input saved={saved} dog={dog} field='name' title='Fullt navn' help='Fullt navn på hunden.' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='shortname' title='Kallenavn' help='Kallenavnet på hunden. Hva roper du på innkalling?' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='gender' type='select' datalist={[['male', 'Male'], ['female', 'Female']]} title='Kjønn' help='Kjønn på hunden. "male", "female"' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='chipId' title='Chip ID' help='Chip ID på hunden. Står ofte i vetrinærhåndbok eller passet.' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='birth' type='date' min='2000-01-01' max='today' title='Fødselsdato' help='Fødselsdato på hunden. Format på dato er: "YYYY-MM-DD".' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='deceased' type='date' min='2000-01-01' max='today' title='Død' help='Dødsdato for hunden. Format på dato er: "YYYY-MM-DD".' onInput={this.updateField} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Hunden er lagret 🐕
					</div>}
				</div>
			</div>
		);
	}
}

export default DogsEdit;
