import { observable, configure, action, computed } from 'mobx';
import StoreModel from 'preact-storemodel';

configure({ enforceActions: 'always' });

class DogStore extends StoreModel {
    constructor() {
        super('dog', {
            namePlural: 'dogs',
            sort: 'title',
            limit: 100,
            api: {
                search: {
                    url: '/api/dogs/',
                    params: {
                        extendedView: 1,
                        limit: 15,
                        sort: 'id',
                    },
                },
                load: {
                    url: '/api/dogs/',
                    params: {},
                },
                save: {
                    url: '/api/dogs/',
                    params: {},
                },
            },
        });
    }

    @observable dog = {};

    @observable dogs = [];

    // @action
    // afterLoad(response) {
    //     console.log(response);
    //     console.log('this.dog', this.dog);
    // }
}

const store = new DogStore();
export default store;
