import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { toJS } from 'mobx';
import linkState from 'linkstate';
import { route } from 'preact-router';

import DatePicker from '../../components/form/datePicker';
// import TimePicker from '../../components/form/timePicker';
import Images from '../../components/form/images';

const initialState = {
    newWorkout: {},
};

const DAY_OF_WEEK = [
    'søndag',
    'mandag',
    'tirsdag',
    'onsdag',
    'torsdag',
    'fredag',
    'lørdag',
];

@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { workoutStore } = this.props.stores;
        workoutStore.updateObjectKeyValue('newWorkout', field, value);
    }

    selectDate = (value) => {
        const { field } = this.props;
        const { workoutStore } = this.props.stores;
        workoutStore.updateObjectKeyValue('newWorkout', field, util.isoDate(value));
    }

    render() {
        const { type, datalist = [], min, max, step, elementtype, field, title, icon, help } = this.props;
        const { workoutStore } = this.props.stores;
        const { newWorkout } = workoutStore;
        let maxValue = max;
        if (type === 'date') {
            if (max === 'today') {
                maxValue = util.isoDate(undefined, false, false, true);
            }
        }
        let displayValue = newWorkout[field];
        if (field === 'duration') {
            displayValue = util.secToHms(newWorkout[field], true);
        }
        if (type === 'datalist') {
            return (
                <div class={`form-group`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    <input list={`list-${field}`} class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        onInput={this.onInput}
                        value={newWorkout[field]}
                    />
                    <datalist id={`list-${field}`} >
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </datalist>
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
                </div>
            );
        }
        return (
            <div class='form-group'>
                <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                {type === 'range' && <span class='float-right'>
                    <strong>{displayValue}</strong>
                </span>}
                <input type={type} class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    min={min}
                    max={maxValue}
                    step={step}
                    onInput={this.onInput}
                    value={newWorkout[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

@observer
class Input extends Component {
    selectDate = (value) => {
        const { workout, type, field, updateField } = this.props;
        if (workout.id) {
            updateField({ id: workout.id, field, value, type });
        }
    }

    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        const { saved, workout, type, datalist = [], min, max, step, elementtype, field, title, icon, help, onInput, updateField } = this.props;
        let value = toJS(workout[field]);
        let maxValue = max;
        if (type === 'date') {
            if (value) {
                value = util.isoDate(workout[field], false, false, true);
            }
            if (max === 'today') {
                maxValue = util.isoDate(undefined, false, false, true);
            }
        }
        let displayValue = value;
        if (field === 'duration') {
            displayValue = util.secToHms(value, true);
        }

        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    {workout.image && <div><img src={workout.image.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} /></div>}
                    <Images saved={saved} object={workout} field={field} updateField={updateField} />
                    {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'datalist') {
            return (
                <div class={`form-group ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    <input list={`list-${field}`} class={`form-control ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                        value={value}
                        data-id={workout.id}
                        data-field={field}
                        onInput={onInput}
                    />
                    <datalist id={`list-${field}`} >
                        {datalist.map(el => (
                            <option value={el[0]}>{el[1]}</option>
                        ))}
                    </datalist>
                    {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }

        return (
            <div class={`form-group ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                {type === 'range' && <span class='float-right'>
                    <strong>{displayValue}</strong>
                </span>}
                <input type={type} class={`form-control ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    min={min}
                    max={maxValue}
                    step={step}
                    value={value}
                    data-id={workout.id}
                    data-field={field}
                    data-type={type}
                    data-elementtype={elementtype}
                    onInput={onInput}
                />
                {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class Dogs extends Component {
    toggleDog(dogId) {
        const { workout, type, elementtype, field, updateField, isNew } = this.props;
        const value = toJS(workout[field]) || [];
        const idx = value.indexOf(dogId);
        if (idx > -1) {
            value.splice(idx, 1);
        } else {
            value.push(dogId);
        }
        updateField({ id: workout.id, field, value, type, elementtype, isNew });
    }

    render() {
        const { userStore } = this.props.stores;
        const { dogs } = userStore;
        const { saved, workout, field, title, icon, help } = this.props;
        const value = toJS(workout[field]) || [];
        // console.log('Dogs.render: type, elementType, value:', type, elementtype, value);
        return (
            <div class={`form-group ${saved[`${field}.${workout.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                <div class='container-fluid px-0'>
                    <div class='row'>
                        {dogs && dogs.map(dog => (
                            <div class='col-6 col-sm-6 col-md-6 col-lg-6 mb-2'>
                                <div class={`border rounded p-2 ${value.indexOf(dog.id) > -1 ? 'bg-success' : ''}`}
                                    onClick={() => this.toggleDog(dog.id)}
                                >
                                    {dog.image ? <img src={dog.image.s3SmallLink} class='img-fluid rounded-circle mr-2' style='width: 30px; height: 30px;' /> : <i class='fas fa-dog mr-2' />}
                                    {dog.shortname || dog.name}
                                    <span class='float-right'>
                                        {dog.birth && <span class='mr-2'>{util.age(dog.birth, dog.deceased)} år</span>}
                                        {dog.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                                    </span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
                {saved[`${field}.${workout.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

