@observer
class WorkoutsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadWorkout() {
		const { workoutStore, userStore } = this.props.stores;
        const { workouts = [] } = userStore;
        const lastWorkout = workouts[0];
        if (this.props.workout === 'new') {
            this.setState({ createNew: true });
            if (lastWorkout) {
                workoutStore.updateObjectKeyValue('newWorkout', 'dogs', lastWorkout.dogs);
                workoutStore.updateObjectKeyValue('newWorkout', 'mushers', lastWorkout.mushers);
                workoutStore.updateObjectKeyValue('newWorkout', 'temperature', lastWorkout.temperature);
                workoutStore.updateObjectKeyValue('newWorkout', 'type', lastWorkout.type);
                workoutStore.updateObjectKeyValue('newWorkout', 'equipment', lastWorkout.equipment);
                workoutStore.updateObjectKeyValue('newWorkout', 'date', util.isoDate(lastWorkout.date, false,  false, true));
                workoutStore.updateObjectKeyValue('newWorkout', 'duration', 3600);
            }
        } else {
            workoutStore.load(this.props.workout);
        }
    }

	resetDog() {
        const { workoutStore } = this.props.stores;
        workoutStore.updateKeyValue('newWorkout', {});
	}

	insertWorkout = async () => {
		const { workoutStore, userStore } = this.props.stores;
		const { newWorkout } = workoutStore;
		await workoutStore.insert({ ...newWorkout });
        this.resetDog();
        userStore.getInfo();
        route(`/workouts/`, true);
	}

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { workoutStore } = this.props.stores;
        let newValue = value;
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            workoutStore.updateObjectKeyValue('newWorkout', field, value);
        } else {
            workoutStore.updateField(id, field, newValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { workoutStore } = this.props.stores;
        await workoutStore.saveField(id, field, value);
    }

    back() {
        route('/workouts/', true);
    }

    componentWillMount() {
        this.loadWorkout();
    }

	render() {
        const { createNew } = this.state;
		const { workoutStore } = this.props.stores;
		const { insertStatus, saved, workout, newWorkout, workoutLocations, isTracking } = workoutStore;
        const { name, date } = workout;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        {createNew ? (
                            <div> 
                                <h5>Legg til ny treningstur</h5>
                                <InputNew stores={this.props.stores} field='date' icon='fas fa-clock' type='date' min='2010-01-01' max='today' title='Dato' help='Dato for turen. Format på dato er: "YYYY-MM-DD".' />

                                <Types stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='type' title='Type tur' help='Type treningstur. "trening", "konkurranse", "tur".' updateField={this.updateFieldValue} />
                                <Tracks stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='track' title='Runde' help='Hvilken treningsrunde er dette.' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='name' title='Tittel' help='Tittel på denne turen. "Fin høsttur i øsende regn..."' />


                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='duration' type='range' min='0' max='28800' step='900' value='3600' icon='fas fa-stopwatch' title='Varighet' help='Hvor lenge var du ute? HH:MI' />
                                    </div>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='speedAvg' icon='fas fa-tachometer-alt' title='Gjennomsnittsfart' help='Gjennomsnittsfart når du var i bevegelse.' />
                                    </div>
                                </div>
                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='distanceKm' icon='fas fa-road' title='Distanse i km' help='Hvor lang var turen i km.' />
                                    </div>
                                    <div class='col-6'>
                                        <InputNew stores={this.props.stores} field='elevation' icon='fas fa-mountain' title='Høydemeter' help='Høydemeter du klatret på denne turen.' />
                                    </div>
                                </div>

                                <Equipments stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='number' field='equipment' title='Utstyr' help='Utstyr som ble brukt. "trollvogn", "atv", "slede".' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='comment' title='Kommentar' help='Kommentar til turen. Hvordan gikk det?' />
                                <InputNew stores={this.props.stores} field='weather' icon='fas fa-cloud-sun-rain' title='Været på turen' help='Hvordan var været.' />
                                <InputNew stores={this.props.stores} field='temperature' type='range' min='-30' max='20' step='1' icon='fas fa-temperature-low' title='Temperatur' help='Hvilken temperatur var det.' />

                                <Dogs stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='array' elementtype='number' field='dogs' title='Hunder' help='Hvilke hunder var med på turen.' updateField={this.updateFieldValue} />
                                <Mushers stores={this.props.stores} isNew={1} saved={saved} workout={newWorkout} type='array' elementtype='number' field='mushers' title='Kjørere' help='Hvem av kjørerne var med på turen.' updateField={this.updateFieldValue} />

                                <InputNew stores={this.props.stores} field='rating' type='range' min='0' max='6' step='1' title='Rating av turen' help='Din rating av turen. 1-6' />

                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertWorkout}
                                >
                                    Registrer ny treningstur
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h5>Endre trening: {name}</h5>
                                <Types stores={this.props.stores} saved={saved} workout={workout} type='number' field='type' title='Type tur' help='Type treningstur. "trening", "konkurranse", "tur".' updateField={this.updateFieldValue} />
                                <Tracks stores={this.props.stores} saved={saved} workout={workout} type='number' field='track' title='Runde' help='Hvilken treningsrunde er dette.' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='name' title='Tittel' help='Tittel på denne turen. "Fin høsttur i øsende regn..."' onInput={this.updateField} />

                                <Input stores={this.props.stores} saved={saved} workout={workout} field='date' icon='fas fa-clock' type='date' min='2010-01-01' max='today' title='Dato' help='Dato for turen. Format på dato er: "YYYY-MM-DD".' onInput={this.updateField} updateField={this.updateFieldValue} />

                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='duration' type='range' min='0' max='28800' step='900' icon='fas fa-stopwatch' title='Varighet i sekunder' help='Hvor lenge var du ute? HH:MI' onInput={this.updateField} />
                                    </div>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='speedAvg' icon='fas fa-tachometer-alt' title='Gjennomsnittsfart' help='Gjennomsnittsfart når du var i bevegelse.' onInput={this.updateField} />
                                    </div>
                                </div>
                                <div class='row px-0'>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='distanceKm' icon='fas fa-road' title='Distanse i km' help='Hvor lang var turen i km.' onInput={this.updateField} />
                                    </div>
                                    <div class='col-6'>
                                        <Input saved={saved} workout={workout} field='elevation' icon='fas fa-mountain' title='Høydemeter' help='Høydemeter du klatret på denne turen.' onInput={this.updateField} />
                                    </div>
                                </div>

                                <Equipments stores={this.props.stores} saved={saved} workout={workout} type='number' field='equipment' title='Utstyr' help='Utstyr som ble brukt. "trollvogn", "atv", "slede".' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='comment' title='Kommentar' help='Kommentar til turen. Hvordan gikk det?' onInput={this.updateField} />
                                <Input saved={saved} workout={workout} field='weather' icon='fas fa-cloud-sun-rain' title='Været på turen' help='Hvordan var været.' onInput={this.updateField} />
                                <Input saved={saved} workout={workout} field='temperature' type='range' min='-30' max='20' step='1' icon='fas fa-temperature-low' title='Temperatur' help='Hvilken temperatur var det.' onInput={this.updateField} />

                                <Dogs stores={this.props.stores} saved={saved} workout={workout} type='array' elementtype='number' field='dogs' title='Hunder' help='Hvilke hunder var med på turen.' updateField={this.updateFieldValue} />
                                <Mushers stores={this.props.stores} saved={saved} workout={workout} type='array' elementtype='number' field='mushers' title='Kjørere' help='Hvem av kjørerne var med på turen.' updateField={this.updateFieldValue} />

                                <Input saved={saved} workout={workout} field='rating' type='range' min='0' max='6' step='1' title='Rating av turen' help='Din rating av turen. 1-6' onInput={this.updateField} />

                                <Input saved={saved} workout={workout} field='image' type='image' title='Bilde' help='Legg til bilde fra treningen.' onInput={this.updateField} updateField={this.updateFieldValue} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Treningen er lagret.
					</div>}

                </div>
                {!createNew && <div class='row'>
					<div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        {isTracking ? <button class='btn btn-danger'
                            onClick={workoutStore.stopTracking}
                        >Stopp sporing</button> : <button class='btn btn-success mr-3'
                            onClick={workoutStore.startTracking}
                        >Start sporing</button>}
                    </div>
                    <div class='offset-0 col-12 offset-sm-1 col-sm-10 offset-md-2 col-md-8 offset-lg-3 col-lg-6'>
                        <xmp>{workoutLocations.map(() => '.')}</xmp>
                    </div>
				</div>}
			</div>
		);
	}
}

export default WorkoutsEdit;
