import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';

import Images from '../../components/form/images';

const initialState = {
    newTeam: {},
};

@observer
class Input extends Component {
    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    render() {
        const { saved, team, field, icon, title, type, help, onInput, updateField } = this.props;
        const value = team[field];
        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${team.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={icon} />} {title}</label>
                    {team.image && <div><img src={team.image.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} /></div>}
                    <Images saved={saved} object={team} field={field} updateField={updateField} />
                    {saved[`${field}.${team.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        return (
            <div class={`form-group ${saved[`${field}.${team.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{title}</label>
                <input type='text' class={`form-control ${saved[`${field}.${team.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={team.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${team.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class TeamsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTeam() {
		const { teamStore } = this.props.stores;
        if (this.props.team === 'new') {
            this.setState({ createNew: true });
        } else {
            teamStore.load(this.props.team);
        }
    }

	resetDog() {
		this.setState({
            newTeam: {},
            createNew: false,
        });
	}

	insertTeam = async () => {
		const { newTeam } = this.state;
		const { teamStore } = this.props.stores;
		await teamStore.insert({ ...newTeam });
        this.resetDog();
        route(`/teams/`, true);
	}

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { teamStore } = this.props.stores;
        let newValue = value;
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            teamStore.updateObjectKeyValue('newDog', field, value);
        } else {
            teamStore.updateField(id, field, newValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { teamStore } = this.props.stores;
        await teamStore.saveField(id, field, value);
    }

    back() {
        route('/teams/', true);
    }

    componentWillMount() {
        this.loadTeam();
    }

	render() {
        const { createNew, newTeam } = this.state;
		const { teamStore } = this.props.stores;
		const { insertStatus, saved, team } = teamStore;
        const { name } = team;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
                        {createNew ? (
                            <div> 
                                <h5>Legg til nytt team</h5>
                                <div class='form-group'>
                                    <label for='nameInput'>Teamnavn</label>
                                    <input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
                                        onInput={linkState(this, 'newTeam.name')}
                                        value={newTeam.name}
                                    />
                                    <small id='nameHelp' class='form-text text-muted'>Navn på teamet. A-laget, B-laget, gjengen på tur, etc.</small>
                                </div>
                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertTeam}
                                >
                                    Registrer nytt team
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h5>Endre team: {name}</h5>
                                <Input saved={saved} team={team} field='image' type='image' title='Bilde' help='Link til bilde av teamet.' onInput={this.updateField} updateField={this.updateFieldValue} />

                                <Input saved={saved} team={team} field='name' title='Teamnavn' help='Navn på teamet. A-laget, B-laget, gjengen på tur, etc.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='description' title='Beskrivelse' help='Beskrivelse av teamet.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='url' title='Link til nettside' help='Link til nettside for teamet.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='facebook' title='Facebook' help='Link til Facebookside.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='instagram' title='Instagram' help='Link til Instagram.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='members' title='Medlemmer' help='Handlere, kjørere som er medlem av teamet.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='dogs' title='Hunder' help='Hunder som er med i teamet.' onInput={this.updateField} />
                                <Input saved={saved} team={team} field='tracks' title='Runder' help='Treningsrundene til teamet.' onInput={this.updateField} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Teamet er lagret.
					</div>}
				</div>
			</div>
		);
	}
}

export default TeamsEdit;
